.bottomsheet__backdrop {
  position: absolute;
  background-color: var(--warna-hitam);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.5;
}
.bottomsheet__isi {
  position: absolute;
  background-color: var(--warna-putih);
  left: 0;
  right: 0;
  bottom: 0;
  top: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1.25rem 1.25rem 0 0;
}